@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700);
@import url(//netdna.bootstrapcdn.com/font-awesome/3.2.1/css/font-awesome.css);
.container {
  height: auto!important;
}


.header {
  background: linear-gradient(15deg, rgba(235,81,125,1) 40%, rgba(252,204,69,0.7) 100%)!important;
  cursor: pointer;
  position: fixed;
  left: 0; right: 0;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}

h3 {
  text-shadow: rgb(0, 0, 0) 2px 0px 0px, rgb(0, 0, 0) 1.75517px 0.958851px 0px, rgb(0, 0, 0) 1.0806px 1.68294px 0px, rgb(0, 0, 0) 0.141474px 1.99499px 0px, rgb(0, 0, 0) -0.832294px 1.81859px 0px, rgb(0, 0, 0) -1.60229px 1.19694px 0px, rgb(0, 0, 0) -1.97998px 0.28224px 0px, rgb(0, 0, 0) -1.87291px -0.701566px 0px, rgb(0, 0, 0) -1.30729px -1.5136px 0px, rgb(0, 0, 0) -0.421592px -1.95506px 0px, rgb(0, 0, 0) 0.567324px -1.91785px 0px, rgb(0, 0, 0) 1.41734px -1.41108px 0px, rgb(0, 0, 0) 1.92034px -0.558831px 0px;
  font-family: 'Montserrat', sans-serif;
}
.favorite {
  -webkit-transition: .2s;
  transition: .2s;
}
.favorite:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

input {
  font-family: monospace;
  font-size: .9rem;
  padding-left: 1em;
  outline: none;
  border-radius: 2em;
  border: none;
  width: 15em;
  height: 2.5em;
}

@media screen and (max-width: 425px) {
  input {
    width: 6em;
  }
}

.preloader {
    width: 100px;
    height: auto;
    border-radius: 10px 0 0 10px;
    position: absolute;
    top: 50%;
    right: 50%;
    -webkit-transform: translate(50%,-50%);
            transform: translate(50%,-50%);
}
.nothing {
  position: absolute;
  top: 50%;
  right: 50%;
  -webkit-transform: translate(50%,-50%);
          transform: translate(50%,-50%);
}
.movies-wrapper {
  margin: 6em auto 0!important;
  width: 65em;
  flex-wrap: wrap;
  padding: 20px!important;
  justify-content: center;
  flex-direction: unset!important;
}

.textWithBlurredBg {
  width: 250px;
  height: auto;
  display: inline-block;
  position: relative;
  -webkit-transition: .3s;
  transition: .3s;
  margin: 1em;
  z-index: 0;
}

.textWithBlurredBg img {
  border-radius: 10px;
  width: 100%;
  height: 375px;
  -webkit-transition: .3s;
  transition: .3s;
}

.heart {
  position: absolute;
  top: 10em!important; right: 8.7em;
  /* display: none; */
}

.heart input {
  z-index: 2!important;
  cursor: pointer;
  font-size: 1.5rem;
  top: -1em!important; left: 5em!important;
  opacity: 0!important;
}

.heart > label::before {
 content: '❤️';
 z-index: 0;
 font-size: 4rem;
 cursor: pointer;
 -webkit-transition: .3s;
 transition: .3s;
 opacity: 0.5!important;
 cursor: pointer;
}

.heart > input:checked ~ label::before {
  opacity: 1!important;
}

.textWithBlurredBg:hover img {
  cursor: pointer;
  -webkit-filter: brightness(60%);
          filter: brightness(60%);
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.textWithBlurredBg :not(img) {
  position: absolute;
  z-index: 1;
  top: 30%;
  width: 100%;
  text-align: center;
  color: #fff;
  opacity: 0;
  -webkit-transition: .3s;
  transition: .3s;
}

.textWithBlurredBg h2 {
  cursor: pointer;
  top: 5%;
}

.textWithBlurredBg p {
  cursor: pointer;
  font-size: 12px;
  top: 20%;
}

.textWithBlurredBg:hover :not(img) {
  opacity: 1;
}

.overlay {
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  -webkit-transition: .5s;
  transition: .5s;
  cursor: pointer;
}

.card {
  display: flex;
  z-index: 2;
  width: 830px;
  height: 450px;
  position: fixed;
  top: 50%;
  right: 50%;
  -webkit-transform: translate(50%, -50%);
          transform: translate(50%, -50%);
  border-radius: 10px;
  box-shadow: 0px 20px 30px 3px rgba(0, 0, 0, 0.55);
}

img {
  width: 300px;
  height: auto;
  border-radius: 10px 0 0 10px;
  position: relative;
}

.card_right {
  position: relative;
  background: rgba(0, 0, 0, 0.96);
  height: 100%;
  border-radius: 0 10px 10px 0;
}

.card_right h2 {
  position: relative;
  cursor: default;
  color: white;
  font-family: 'Montserrat', sans-serif;
  font-size: 1.4rem;
  margin: 30px 0 0 0;
  padding: 0 0 0 40px;
}

.card_right__details ul {
  list-style-type: none;
  padding: 0 0 0 40px;
  margin: 10px 0 0 0;
}

.card_right__details ul li {
  display: inline;
  color: #e3e3e3;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  font-size: 14px;
  padding: 0 40px 0 0;
}

.card_right__rating {
  position: absolute;
  left: 2.3em;
  cursor: pointer;
  margin: 10px 0 10px 0;
}

.card_right__rating >input {
  display: none;
}

.card_right__rating >label:before {
  margin: 5px;
  font-size: 1.25em;
  display: inline-block;
  content: "\f005";
  font-family: FontAwesome;
}

.card_right__rating>label {
  color: #ddd;
  float: right;
}

.card_right__rating >input:checked~label, .card_right__rating:not(:checked)>label:hover, .card_right__rating:not(:checked)>label:hover~label {
  cursor: pointer;
  color: #896dbe;
}

.card_right__rating > input:checked+label:hover, .card_right__rating > input:checked~label:hover, .card_right__rating > label:hover~input:checked~label, .card_right__rating > input:checked~label:hover~label {
  color: #896dbe;
}

.card_right__review p {
  color: white;
  font-family: 'Montserrat', sans-serif;
  font-size: 12px;
  padding: 3em 40px 0 40px;
  letter-spacing: 1px;
  margin: 10px 0 10px 0;
  line-height: 20px;
}

.card_right__review a {
  text-decoration: none;
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
  padding: 0 0 0 40px;
  color: #ddcdfc;
  margin: 0;
}

#genres li {
  padding: 0 10px 0 0;
  font-size: 12px;
}

@media screen and (max-width: 700px) {

  .card {
    height: auto;
    top: 53%;
    width: 100%;
  }

  .card img {
    display: none;
  }

  .card_right {
    padding: 1em;
    border-radius: 10px;
  }

  .card_right__details div:first-child {
    padding-right: 0!important;
  }

  .card_right__details #genres {
    margin-right: 0;
  }

  .card_right__details ul {
    padding: 0;
    margin-right: 20px;
  }

  .card_right__rating {
    left: 0;
  }

  .card_right h2 {
    font-size: 1.4rem;
    margin: 0;
    padding: 0;
  }

  .card_right__details ul li {
    font-size: 13px;
    padding: 0 5px 0 0;
  }

  .card_right__review p {
    padding: 0;
    padding-top: 3em;
  }
}

